
export const remBase = 14

let htmlFontSize
!(function() {
  const calc = function() {
    const html = document.getElementsByTagName('html')[0]
    const width = html.clientWidth
	let size = remBase * (width / 1920)
    html.style.fontSize = size + 'px'
	htmlFontSize = size
  }
  calc()
  window.addEventListener('resize', calc)
})()
